/* Styling related to SupOps pages */
a.button.supops_next_slide {
    align-items: center;
    display: flex;
    flex-flow: row nowrap;
    inline-size: 98%;
    justify-content: space-around;
    text-align: center;

    & img {
        block-size: 1rem;
    }
}

img.supops_fact {
    block-size: 8rem;
    inline-size: 8rem;
    margin: 1rem;
}

@counter-style suppos_list_resolutions {
    suffix: ' ';
    symbols:          ;
    system: fixed;
}

.suppos_list_resolutions {
    list-style-type: suppos_list_resolutions;
}

@counter-style suppos_list_needs {
    suffix: ' ';
    symbols:       ;
    system: fixed;
}

.suppos_list_needs {
    list-style-type: suppos_list_needs;
}